}
.trends h1 {
    font-size: 12px;
    font-weight: 600;
    margin: 0;
    font-family: 'Poppins',sans-serif;
    padding-bottom: 8px;
    margin-bottom: 8px;
    border-bottom: 1px solid #e0e0e0;
}

.trend p:first-child{
    font-size: 16px;
    font-weight: 600;
    margin:0;
