.tweet-form{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding: 20px;
    color:var(--gray1);
}
.tweet-form h1 {
    font-size: 12px;
    font-weight: 600;
    margin: 0;
    font-family: 'Poppins',sans-serif;
    padding-bottom: 8px;
    margin-bottom: 8px;
    border-bottom: 1px solid #e0e0e0;
}
.tweet-form .img{
    width: 40px;
    border-radius: 8px;
    overflow: hidden;
    margin-right: 12px;
}
.tweet-form textarea{
    width: 100%;
    outline: none;
    resize:none;
    border: none;
    display: block;
    background-color: transparent;
    font-size: 16px;
    height:28px;
    color:var(--gray1);
}
.tweet-form .text {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
}
.tweet-form .tweet-foot{
    margin-left: 50px;
    justify-content: space-between;
    display: flex;
    align-items: center;
}

.tweet-form .settings{
    color:var(--main)
}
.tweet-form .settings i:first-child{
    margin-right: 7px;
    
}
.tweet-form .settings i:nth-child(2){
    margin-right: 14px;
    cursor: pointer;
}
.tweet-form .who-settings{
    display: none;
    position: absolute;
    color:var(--gray1);
    border: 1px solid #E0E0E0;
    border-radius: 10px;
    padding: 8px 15px;
    margin-top: 8px;
    font-size: 12px;
    background-color: #fff;
}
.tweet-form .who-settings p{
    margin: 0;
}

.tweet-form .who-settings > p:nth-child(1){
    margin-bottom: 5px;
    font-weight: 600;
}
.tweet-form .who-settings > p:nth-child(2){
    margin-bottom: 10px;
    color:var(--gray3)
}
.who-settings .item{
    padding: 10px;
    cursor: pointer;
}
.who-settings .item:hover{
    background-color: #f2f2f2;
    border-radius: 8px;
}
.tweet-form button{
    background-color: var(--main);
    color: #fff;
    border: none;
    padding: 8px 20px;
    font-size: 12px;
    border-radius: 8px;
}
.toggle{
    cursor: pointer;
}

#toggleWho:checked ~ .who-settings{
    display: block;
}
@media(max-width:600px){
    .tweet-form .tweet-foot{margin-left:0; font-size: 14px; }
}
.tweet{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding:20px;
    padding-top: 17px;
    color:var(--gray1);
}
.tweet .tweet-user{
    display:flex;
    align-items: center;
}

.tweet-user p{
    font-size: 16px;
    font-weight: 600;
    margin:0
}
.tweet a{
    color:var(--gray1);
    text-decoration: none;
}
.tweet-user p span{
    font-size: 12px;
    font-weight: 500;
    color:var(--gray4)
}
.tweet .nums{
    font-size: 12px;
    color:var(--gray4);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    grid-gap: 16px;
    padding-bottom: 8px;
}
.tweet .nums p{
    margin: 0;
}

.tweet .buttons{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
    padding:5px
}
.tweet .buttons button{
    display: flex;
    justify-content: center;
    grid-gap: 12px;
    align-items: center;
    border:none;
    background-color: transparent;
    color:var(--gray);
    padding:15px;
    border-radius: 8px;
    cursor: pointer;
}

.tweet .buttons button:hover{
    background-color: #f2f2f2;
}

.tweet .reply{
    display: grid;
    grid-template-columns:auto 1fr ;
    margin-top: 10px;
}

.tweet .reply .input{
    font-size: 14px;
    background: #FAFAFA;
    border: 1px solid #F2F2F2;
    border-radius: 8px;
    align-items: center;
    padding: 0 12px;
    width: 100%;
    display: flex;
}
.tweet .input input{
    border:0;
    outline: none;
    width: 100%;
    background-color: transparent;
}
.tweet .input input::-webkit-input-placeholder{
    color: var(--gray4);
}
.tweet .input input:-ms-input-placeholder{
    color: var(--gray4);
}
.tweet .input input::placeholder{
    color: var(--gray4);
}
.tweet .input i{
    color: var(--gray4);
    cursor: pointer;
}


.tweet .media{
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 10px;
}


@media(max-width:680px) {
    .tweet button span{
        display: none;
    }
} 

