.trends{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 30px;
    color:var(--gray1);
    height: fit-content;
}
.trend{
    margin-bottom: 20px;
}
.trend:last-child{
    margin-bottom: 0;
}
.trends h1 {
    font-size: 12px;
    font-weight: 600;
    margin: 0;
    font-family: 'Poppins',sans-serif;
    padding-bottom: 8px;
    margin-bottom: 8px;
    border-bottom: 1px solid #e0e0e0;
}

.trend p:first-child{
    font-size: 16px;
    font-weight: 600;
    margin:0;
    margin-bottom: 8px;
}
.trend p:last-child{
    margin:0;
    font-size: 12px;
    font-weight: 500;
    color:var(--gray3)
}