.tweet{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding:20px;
    padding-top: 17px;
    color:var(--gray1);
}
.tweet .tweet-user{
    display:flex;
    align-items: center;
}

.tweet-user p{
    font-size: 16px;
    font-weight: 600;
    margin:0
}
.tweet a{
    color:var(--gray1);
    text-decoration: none;
}
.tweet-user p span{
    font-size: 12px;
    font-weight: 500;
    color:var(--gray4)
}
.tweet .nums{
    font-size: 12px;
    color:var(--gray4);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    grid-gap: 16px;
    padding-bottom: 8px;
}
.tweet .nums p{
    margin: 0;
}

.tweet .buttons{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
    padding:5px
}
.tweet .buttons button{
    display: flex;
    justify-content: center;
    grid-gap: 12px;
    align-items: center;
    border:none;
    background-color: transparent;
    color:var(--gray);
    padding:15px;
    border-radius: 8px;
    cursor: pointer;
}

.tweet .buttons button:hover{
    background-color: #f2f2f2;
}

.tweet .reply{
    display: grid;
    grid-template-columns:auto 1fr ;
    margin-top: 10px;
}

.tweet .reply .input{
    font-size: 14px;
    background: #FAFAFA;
    border: 1px solid #F2F2F2;
    border-radius: 8px;
    align-items: center;
    padding: 0 12px;
    width: 100%;
    display: flex;
}
.tweet .input input{
    border:0;
    outline: none;
    width: 100%;
    background-color: transparent;
}
.tweet .input input::placeholder{
    color: var(--gray4);
}
.tweet .input i{
    color: var(--gray4);
    cursor: pointer;
}


.tweet .media{
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 10px;
}


@media(max-width:680px) {
    .tweet button span{
        display: none;
    }
} 
