.users-list-container{
    position: fixed;
    width: 100%;
    height: 100vh;
    display: flex;
    top:0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.1);
    align-items: center;
    justify-content: center;
    z-index: 110;
    padding: 15px;
}
.users-list {
    max-width: 700px;
    overflow-y: auto;
    max-height: 80vh;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding: 20px;
    color:var(--gray1);
    min-width: 190px;
}
.user-list a{
    color:var(--gray1);
    text-decoration: none;
}
.users-list h1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    font-weight: 600;
    margin: 0;
    font-family: 'Poppins',sans-serif;
    padding-bottom: 8px;
    border-bottom: 1px solid #e0e0e0;
}
.users-list h1 i{
    cursor: pointer;
    color:var(--gray2);
    transform: translate(-8px,-4px) scale(1.7);
}

.user-list{
    display: flex;
    padding: 20px 0;
    border-bottom: 1px solid #e0e0e0;
}
.user-list:last-child{
    padding-bottom: 0;
    border-bottom:none;
}
.user-list .img{
    width: 40px;
    height: 40px;
    float:left;
    border-radius: 8px;
    overflow: hidden;
    margin-right: 15px;
}

.user-list-main h2{
    font-size: 16px;
    margin:0;
}
.user-list-main p{
    margin:0;
    font-size: 12px;
    color:var(--gray3)
}
.user-list .bio{
    font-size: 14px;
    color:var(--gray3);
    margin-bottom: 0;
}
.user-list button{
    background-color: var(--main);
    color: #fff;
    border: none;
    align-self: flex-start;
    padding: 4px 15px;
    display: flex;
    grid-gap: 5px;
    align-items: center;
    font-size: 12px;
    border-radius: 4px;
}