.filter{
    background-color: var(--style);
    border-radius: 8px;
    font-size: 14px;
    padding: 5px 0;
    min-width: 170px;
    align-self:flex-start;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}
.filter p{
    margin:0;
    padding: 10px 20px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    color:var(--gray3);
    position: relative;
    margin: 3px 0;
}
.filter .actual{
    color:var(--main)
}
.filter .actual::before{
    position: absolute;
    content: '';
    width: 3px;
    background-color: var(--main);
    height: 100%;
    top:0;
    border-radius: 0 10px 10px 0;
    left: 0;
}
