.main-user {
    margin-bottom: -25px;
}

.main-user>.img {
    height: 294px;
    overflow: hidden;
    width: 100%;
    z-index: 0;
}

.user-data {
    display: grid;
    grid-gap: 15px;
    grid-template-columns: auto 2fr auto;
    background-color: var(--style);
    padding: 19px 25px;
    justify-content: center;
    width: 74%;
    margin: auto;
    border-radius: 12px;
    transform: translateY(-50px);
    z-index: 1;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}

.user-data p {
    margin: 0;
}

.user-data .img {
    width: 160px;
    height: 160px;
    min-width: 160px;
    overflow: hidden;
    padding: 4px;
    background-color: #fff;
    border-radius: 8px;
    transform: translateY(-70px);
}

.user-data img {
    border-radius: 4px;
}

.user-text-head {
    display: flex;
    align-items: baseline;
    grid-gap: 26px;
    font-size: 12px;
    font-weight: 600;
    margin-bottom: 22px;
}
.user-text-head p:nth-child(2), .user-text-head p:nth-child(3){
    cursor: pointer;
}
.user-text-head p:nth-child(2):hover, .user-text-head p:nth-child(3):hover{
    text-decoration: underline;
}

.user-text-head span {
    font-weight: 500;
    color: var(--gray2)
}

#bigUserName {
    font-size: 24px;
    font-family: 'Poppins', sans-serif;
    margin-right: 16px;
}

.user-bio {
    max-width: 500px;
    color: var(--gray3);
    font-size: 18px;
}

.user-data button {
    background-color: var(--main);
    color: #fff;
    border: none;
    align-self: flex-start;
    padding: 8px 20px;
    font-size: 12px;
    border-radius: 4px;
}



@media(max-width:820px) {
    .user-data {
        grid-template-columns: auto;
        width: 90%;
    }
    .user-data .img {
        margin: auto;
        margin-bottom: -70px;
    }
    .user-text-head {
        flex-wrap: wrap;
        justify-content: center;
        grid-gap: 15px;
        margin-bottom: 15px;
    }
    #bigUserName {
        text-align: center;
        width: 100%;
    }
    .user-bio {
        text-align: center;
    }
    .user-data button {
        width: fit-content;
        justify-self: center;
    }
    .user-data{
        margin-top: -50px;
    }
    .main-user>.img {
        min-height: 240px;
        height: auto;
    }
}

