
.search-box{
    background-color: #fff;
    border-radius: 8px;
    display: flex;
    padding: 8px 10px;
    align-items: center;
    margin-bottom: 15px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}
.search-box i{
    color:var(--gray4);
    margin-right: 10px;
}
.search-box input{
    font-size: 16px;
    width: 100%;
    outline: none;
    border:none;
    color:var(--gray1)
}
.search-box input::placeholder{
    color:var(--gray4)
}
.search-box button{
    padding: 8px 24px;
    border-radius: 4px;
    background-color: var(--main);
    color:#fff;
    border:none;
    font-size: 12px ;
}