header {
    background-color: var(--style);
    display: flex;
    justify-content: space-between;
    padding: 0 5vw;
}

header p {
    margin: 0;
    color: #333
}

.logo {
    display: flex;
    align-items: center;
    user-select: none;
}

.logo p {
    font-weight: 600;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
}

.logo img {
    -webkit-user-drag: none;
    margin-right: 10px;
}

.tabs {
    display: flex;
    font-size: 14px;
    font-family: 'Poppins', sans-serif;
    margin-top: 29px;
    grid-gap: 5vw;
}

.tabs p {
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    color: var(--gray3);
    position: relative;
    display: flex;
}
.tabs a{
    display: block;
    padding: 0 10px 15px 10px;
    text-decoration: none;
    color:var(--gray1)
}

.tabs .actual a{
    color: var(--main)
}

.tabs .actual::after {
    content: " ";
    position: absolute;
    height: 3px;
    background-color: var(--main);
    width: 100%;
    left: 0;
    bottom: 0;
    border-radius: 20px 20px 0 0;
}

.user {
    display: flex;
    align-items: center;
    font-size: 12px;
    font-weight: 700;
    position: relative;
    font-family: 'Noto sans', sans-serif;
}

.user .img {
    border-radius: 8px;
    width: 32px;
    height: 32px;
    overflow: hidden;
    margin-right: 10px;
}

.user>label i {
    cursor: pointer;
    margin-left: 20px;
}

.img img {
    width: 100%;
    height: fit-content;
}

.menu {
    position: absolute;
    border: 1px solid #E0E0E0;
    border-radius: 10px;
    padding: 8px 15px;
    width: 200px;
    min-width: 160px;
    right: 0;
    display: none;
    margin-top: 8px;
    top: 100%;
    background-color: #fff;
    z-index: 100;
}

.menu i {
    margin-right: 10px;
    width: 20px;
    color: inherit;
}

.menu p {
    white-space: nowrap;
    font-size: 12px;
    padding: 10px 15px;
    margin: 0;
    border-radius: 10px;
    color: #4F4F4F;
    cursor: pointer
}

.menu p:hover {
    background-color: #eee
}

.menu p:last-child {
    color: #EB5757
}

#toggleMenu {
    display: none;
}

#toggleMenu:checked~.menu, #toggleMenu:checked~.close-menu {
    display: block;
}

.tabs2 {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 50;
    background-color: var(--style);
    width: 100%;
    display: none;
}
.tabs2 a{
    color:var(--gray3);
    padding-top: 14px;
    width: 100%;
    padding:20px 10px;
}
.tabs2 p {
    text-align: center;
    display: flex;
    font-size: 20px;
}

.menu {
    -webkit-animation: swing-in-top-fwd .5s cubic-bezier(.175, .885, .32, 1.275) both;
    animation: swing-in-top-fwd .5s cubic-bezier(.175, .885, .32, 1.275) both
}

@-webkit-keyframes swing-in-top-fwd {
    0% {
        -webkit-transform: rotateX(-100deg);
        transform: rotateX(-100deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 0;
    }
    100% {
        -webkit-transform: rotateX(0deg);
        transform: rotateX(0deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 1;
    }
}

@keyframes swing-in-top-fwd {
    0% {
        -webkit-transform: rotateX(-100deg);
        transform: rotateX(-100deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 0;
    }
    100% {
        -webkit-transform: rotateX(0deg);
        transform: rotateX(0deg);
        -webkit-transform-origin: top;
        transform-origin: top;
        opacity: 1;
    }
}

@media(max-width:680px) {
    header {
        padding: 15px 5vw;
        position: sticky;
        top: 0;
        z-index: 50
    }
    header .user-name {
        display: none;
    }
    .menu{
        top: 50px;
    }
    .user>label i {
        margin: 0;
    }
    header .tabs {
        display: none;
    }
    .tabs2 {
        display: grid !important;
        grid-gap: 5vw;
        grid-template-columns: repeat(3, auto);
    }
}