@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&family=Poppins&display=swap');    :root{
  --main:#2f80ed;
  --background: #f2f2f2;
  --style:#fff;
  --gray1:#333;
  --gray2:#999;
  --gray3:#828282;
  --gray4:#BDBDBD
}
*{
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
}

button{
  cursor: pointer;
}
body {
  margin: 0;
  background-color: var(--background);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
hr{
  border: 1px solid #E0E0E0;
  border-bottom: none;
}

.contenedor{
  display: grid;
  grid-template-columns: auto 1fr;
  width: 74%;
  margin: auto;
  grid-gap: 24px;
  grid-row-gap: 15px;
  overflow: hidden;
}
.contenedor2{
  display: grid;
  grid-template-columns: 1fr auto;
  width: 74%;
  grid-row-gap: 15px;
  margin: auto;
  grid-gap: 24px;
  overflow: hidden;
}

.tweets{
  display: grid;
  grid-gap: 30px;
}
.icon{
  width: 40px;
  height: 40px;
  border-radius: 8px;
  overflow: hidden;
  margin-right: 18px;
}
.img{
  display: flex;
}

@media(max-width:840px){
  .contenedor, .contenedor2{
    grid-template-columns: auto;
    width: 90%;
  }
}

@media(max-width:940px){
  .contenedor2{
    grid-template-columns: auto;
  }
}

@media(max-width:680px){
  .contenedor,.contenedor2{
    padding-bottom: 85px;
  }
}