.to-follow{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    background-color: var(--style);
    border-radius: 8px;
    padding: 20px;
    color:var(--gray1);
    min-width: 190px;
}
.to-follow h1 {
    font-size: 12px;
    font-weight: 600;
    margin: 0;
    font-family: 'Poppins',sans-serif;
    padding-bottom: 8px;
    border-bottom: 1px solid #e0e0e0;
}

.to-follow a{
    color:var(--gray1);
    text-decoration: none;
}
.follow-user .data{
    display: flex;
    justify-content: space-between;
    grid-gap: 14px;
    align-items: center;
}
.follow-user .data h2{
    font-size: 16px;
    font-weight: 500;
    margin:0 
}
.follow-user .data p{
    font-size: 12px;
    margin:0;
    color:var(--gray3)
}
.follow-user .data button{
    background-color: var(--main);
    color: #fff;
    border: none;
    padding: 4px 8px;
    font-size: 12px;
    border-radius: 4px;
}
.follow-user .data .img{
    width: 40px;
    height: 40px;
    border-radius: 8px;
    overflow: hidden;
}


.to-follow p{
    color: var(--gray3);
    font-size: 14px;
}
.to-follow .img{
    border-radius: 8px;
    overflow: hidden;
}

.follow-user{
    padding: 20px 0;
    border-bottom: 1px solid #e0e0e0;
}
.follow-user:last-child{
    padding-bottom: 0;
    border-bottom: none;
}